'use client';
import React from 'react';
import { Box, CssBaseline, AppBar, Toolbar, Typography, Drawer, List, ListItemButton, ListItemText } from '@mui/material';
import Link from 'next/link';
import { useSession } from '@/features/auth/session';
const drawerWidth = 240;

export function AppShell({ children }: { children: React.ReactNode }) {
  const { session } = useSession();
  return (
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      <AppBar position="fixed" sx={{ zIndex: (t) => t.zIndex.drawer + 1 }}>
        <Toolbar>
          <Typography variant="h6" noWrap sx={{ flex: 1 }}>AthleteNarrative Back Office</Typography>
        </Toolbar>
      </AppBar>
      <Drawer variant="permanent" sx={{ width: drawerWidth, '& .MuiDrawer-paper': { width: drawerWidth, boxSizing: 'border-box' }}}>
        <Toolbar />
        <List>
          <ListItemButton LinkComponent={Link} href="/dashboard"><ListItemText primary="Dashboard" /></ListItemButton>
          {session?.role === 'parent' && (
            <ListItemButton LinkComponent={Link} href="/agencies"><ListItemText primary="Agencies" /></ListItemButton>
          )}
          <ListItemButton LinkComponent={Link} href="/clients"><ListItemText primary="Clients" /></ListItemButton>
          <ListItemButton LinkComponent={Link} href="/ai-prompts"><ListItemText primary="AI Prompts" /></ListItemButton>
          <ListItemButton LinkComponent={Link} href="/recruiter"><ListItemText primary="Recruiter" /></ListItemButton>
        </List>
      </Drawer>
      <Box component="main" sx={{ flexGrow: 1, p: 3 }}>
        <Toolbar />
        {children}
      </Box>
    </Box>
  );
}


